import React from 'react';
import classNames from 'classnames';
import { SectionSplitProps } from '../../utils/SectionProps';
import SmallSplit from '../sections/partials/SmallSplit';
import NecoleBringsYouSection from '../sections/partials/NecoleBringsYouSection';

const propTypes = {
  ...SectionSplitProps.types
}

const defaultProps = {
  ...SectionSplitProps.defaults
}

const NecoleBringsYou = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {

  const outerClasses = classNames(
    'features-split section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-split-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const splitClasses = classNames(
    'split-wrap',
    invertMobile && 'invert-mobile',
    invertDesktop && 'invert-desktop',
    alignTop && 'align-top'
  );
  return (
    <section
      {...props}
      className={outerClasses}
      id="benefits"
    >
      <div className="container">
        <div className={innerClasses}>
          <h2 className="center-content"><span className="text-color-primary ">necole</span> <span className="m-0 fw-400 text-color-low">brings you...</span></h2>
          <div className={splitClasses}>

            <NecoleBringsYouSection
              title="Motivation"
              subtitle={<>Stay accountable with <span className="text-color-primary fw-600">necole</span> "as your guide." </>} 
              smallsplit1={<><SmallSplit
                              title="Nudges"
                              text="Get notifications on learning opportunities up to 3 times a week."
                              imgsrc={require('./../../assets/images/bell-ringing.png')}
                              alt="bell-ringing"
                              /></>}
              smallsplit2={<><SmallSplit
                              title="Community"
                              text="Be inspired by fellow users & experts."
                              imgsrc={require('./../../assets/images/community.png')}
                              alt="community"
                            /></>}            
              bottomtext1="The best of intentions don’t always lead to action."                
              bottomtext2="What keeps you going?"
              vidsrc={require('./../../assets/images/NECOLE_Motivation to learn.mp4')}              
              />

            <NecoleBringsYouSection
              title="Convenience"
              subtitle="Learn your way - anytime, anywhere."
              smallsplit1={<><SmallSplit
                              title="Bite-Sized Mobile Content"
                              text="Consume thousands of content via mobile in under 10 minutes."
                              imgsrc={require('./../../assets/images/bite-sized.png')}
                              alt="bite-sized"
                            /></>}
              smallsplit2={<><SmallSplit
                              title="Multi-Format Content"
                              text="Read, watch or listen to videos, articles, podcasts, webinars & interactive courses."
                              imgsrc={require('./../../assets/images/multi-format.png')}
                              alt="multi-format"
                            /></>}            
              bottomtext1="Time is scarce."                
              bottomtext2="What will you choose to do with it?"
              vidsrc={require('./../../assets/images/NECOLE_Learn anywhere anytime.mp4')}
              vidclassname="convenience-vid"
              />     

            <NecoleBringsYouSection
              title="Expertise"
              subtitle="Learn smarter & faster with expert guidance."
              smallsplit1={<><SmallSplit
                              title="Curation Process"
                              text="Consume thousands of content via mobile in under 10 minutes."
                              imgsrc={require('./../../assets/images/curation.png')}
                              alt="curation"
                            /></>}
              smallsplit2={<><SmallSplit
                              title="Exclusive Original Content"
                              text="Access our L52 pathways & webinars for a truly unique learning experience."
                              imgsrc={require('./../../assets/images/exclusive.png')}
                              alt="exclusive"
                            /></>}            
              bottomtext1="Do you feel overwhelmed?"                
              bottomtext2="Be guided by a leadership development authority."
              vidsrc={require('./../../assets/images/NECOLE_Expert content.mp4')}
              />                
          </div>
        </div>
      </div>
    </section>
  );
}

NecoleBringsYou.propTypes = propTypes;
NecoleBringsYou.defaultProps = defaultProps;

export default NecoleBringsYou;