import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import VerticalTextFade from '../sections/partials/VerticalTextFade';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const FutureReady = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );


  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="spacer-32" />
      <div className="futureready-banner spacer-64">
        <div className="container">
          <div className={innerClasses}>
            <h2 className="ta-c fw-600">Top future-ready skills <span className="fw-400">covered by our experts</span></h2>
            <h3 className="fw-400 ta-c">
              <VerticalTextFade
                textArray={["Complex Problem Solving",
                  "Critical Thinking",
                  "Creativity",
                  "People Management",
                  "Coordinating with Others",
                  "Emotional Intelligence",
                  "Judgement and Decision Making",
                  "Service Orientation",
                  "Negotiation",
                  "Cognitive Flexibility"
                ]}
                interval={1200} />
            </h3>
          </div>
          <div className="spacer-64" />
        </div>
      </div>
    </section>
  );
}

FutureReady.propTypes = propTypes;
FutureReady.defaultProps = defaultProps;

export default FutureReady;