import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Input from '../elements/Input';
import Modal from '../elements/Modal';
import Button from '../elements/Button';
import { Link } from 'react-router-dom';
import emailjs from 'emailjs-com';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

class ContactForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      message: "",
      showModalSuccess: false,
      showModalError: false,
      sending: false
    };
    this.submitForm = this.submitForm.bind(this)
    this.onChangeName = this.onChangeName.bind(this)
    this.onChangeEmail = this.onChangeEmail.bind(this)
    this.onChangeMessage = this.onChangeMessage.bind(this)
  }

  onChangeName(e) {
    this.setState({ name: e.target.value });
  }

  onChangeEmail(e) {
    this.setState({ email: e.target.value });
  }

  onChangeMessage(e) {
    this.setState({ message: e.target.value });
  }

  openModalSuccess = (e) => {
    e.preventDefault();
    this.setState({ showModalSuccess: true });
  }

  closeModalSuccess = (e) => {
    e.preventDefault();
  }

  openModalError = (e) => {
    e.preventDefault();
    this.setState({ showModalError: true });
  }

  closeModalError = (e) => {
    e.preventDefault();
    this.setState({ showModalError: false, error: "" });
  }

  validateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  submitForm(e) {
    e.preventDefault();

    var self = this;
    var params = {
      name: this.state.name,
      email: this.state.email,
      message: this.state.message
    };

    if (!this.validateEmail(this.state.email)) {
      self.setState({ error: "Please enter a valid email address", showModalError: true });
    }
    else if (this.state.name.length === 0 || this.state.message.length === 0) {
      self.setState({ error: "Please ensure all fields are filled", showModalError: true });
    }
    else {
      self.setState({ sending: true });
      emailjs.send('leaderonomics_digital', 'template_iVFwJf94', params, 'user_gTr4ukkRJbDaewFscseU1')
        .then(function (response) {
          self.setState({
            name: "",
            email: "",
            message: ""
          });

          self.setState({ showModalSuccess: true });
        }, function (error) {
          var errorMessage = "There has been an error: " + error;
          self.setState({ showModalError: true, error: errorMessage });
        }).then(function(){
          self.setState({ sending: false });
        });
    }

    return;
  }

  render() {
    return <>
      <form>
        <fieldset>
          <div className="mb-16">
            <Input
              type="text"
              placeholder="Name"
              label="Name"
              value={this.state.name}
              onChange={this.onChangeName}
              required />
          </div>
          <div className="mb-16">
            <Input
              type="email"
              placeholder="Email"
              label="Email"
              value={this.state.email}
              onChange={this.onChangeEmail}
              required />
          </div>
          <div className="mb-16">
            <Input
              type="textarea"
              placeholder="Write a message here"
              label="Message"
              value={this.state.message}
              onChange={this.onChangeMessage}
              required
            />
          </div>
          <div className="mt-24">
            <div className="button-group">
              <Button color="primary" onClick={this.submitForm} loading={this.state.sending}>Submit</Button>
            </div>
          </div>
        </fieldset>
      </form>

      {/* Success modal */}
      <Modal
        show={this.state.showModalSuccess}
        handleClose={this.closeModalSuccess}
        className="invert-color"
        closeHidden
      >
        <div className="m-0 center-content">Thanks for contacting us, we'll be in touch with you soon!</div>
        <div className="pt-12 center-content">
          <Link to=""><Button>Return</Button></Link>
        </div>
      </Modal>

      {/* Error modal */}
      <Modal
        show={this.state.showModalError}
        handleClose={this.closeModalError}
        className="invert-color"
      >
        <div className="m-0 center-content text-color-error">{this.state.error}</div>
      </Modal>
    </>;
  }
}

const Contact = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    'container',
    // 'invert-color',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className={innerClasses}>
        <h2 className="text-color-primary">Contact Us</h2>
        <ContactForm />
      </div>
    </section>
  );
}

Contact.propTypes = propTypes;
Contact.defaultProps = defaultProps;

export default Contact;