import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import TextLoop from "react-text-loop";

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const YourTime = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="time-banner">
        <div className={innerClasses}>
          <div className="spacer-64" />
          <div className="spacer-48" />
          <h2 className="fw-400 ta-c">Your <TextLoop interval={1800} mask={true}>
            <span><em>time </em></span>
            <span><em>career</em></span>
            <span><em>future</em></span>
            <span><em>development</em></span>
            <span><em>learning</em></span>
            <span><em>growth</em></span>
            <span><em>upskilling</em></span>
          </TextLoop> is important. So...</h2>
          <div className="spacer-64" />
          <div className="spacer-48" />
        </div>
      </div>
    </section>
  );
}

YourTime.propTypes = propTypes;
YourTime.defaultProps = defaultProps;

export default YourTime;