import React from 'react';
import classNames from 'classnames';

const FooterNav = ({
  className,
  ...props
}) => {

  const classes = classNames(
    'footer-nav',
    className
  );

  return (
    <nav
      {...props}
      className={classes}
    >
      <ul className="list-reset">
        <li>
          <a href={`${process.env.PUBLIC_URL}/terms/`} target="_blank" rel="noopener noreferrer">Terms of Use</a>
        </li>
        <li>
          <a href={`${process.env.PUBLIC_URL}/privacy`} target="_blank" rel="noopener noreferrer">Privacy Policy</a>
        </li>
        <li>
          <a href={`${process.env.PUBLIC_URL}/contact`} target="_blank" rel="noopener noreferrer">Contact Us</a>
        </li>
      </ul>
    </nav>
  );
}

export default FooterNav;