import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const FutureInvestment = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container center-content" id="pricing">
        <div className={innerClasses}>
            <h2 className="text-color-primary">Your future <span className="fw-400">is what you invest in today.</span></h2>
            <div className="text-color-med invert-color">
                <div id="subscribe">“94% of millennials reported making personal improvement commitments </div>
                <div className="mt-0"><b>and said they’d be willing to spend nearly $300 a month on self-improvement</b>”</div>
                <div><i>- Brand Minds 2020</i></div>
            </div>
        </div>
      </div>
    </section>
  );
}

FutureInvestment.propTypes = propTypes;
FutureInvestment.defaultProps = defaultProps;

export default FutureInvestment;