import React from 'react';
import classNames from 'classnames';

const NecoleBringsYouSection = ({
    title,
    subtitle,
    smallsplit1,
    smallsplit2,
    bottomtext1,
    bottomtext2,
    classNameImg,    
    vidsrc,
    vidref,
    vidclassname,
    ...props
}) => {

    return (
        <>
            <div className="split-item screen-reader"></div>
            <div className="split-item">
                <div className="split-item-content center-content-mobile reveal-from-left" data-reveal-container=".split-item">
                    <h2 className="text-color-primary fw-600 mb-8"> {title} </h2>
                    <div>
                        <h5 className="mt-0 mb-12 fw-400 invert-color">
                            <span className="text-color-high"> {subtitle} </span>
                        </h5>
                    </div>
                    <div className="reveal-from-left" data-reveal-delay="200">
                        {smallsplit1}
                        {smallsplit2}
                        <div className="ta-l reveal-from-bottom center-content-mobile" data-reveal-delay="100">
                            <h4 className="invert-color p-0 mb-0 fw-200">{bottomtext1}</h4>
                            <h4 className="invert-color p-0 m-0 fw-400">{bottomtext2}</h4>
                        </div>
                    </div>
                </div>
                <div className={
                    classNames(
                        'split-item-image center-content-mobile reveal-from-bottom',
                        props.imageFill && 'split-item-image-fill'
                    )}
                    data-reveal-container=".split-item">
                    <video autoPlay loop muted playsInline ref={vidref} className={vidclassname}>
                        <source src={vidsrc} type="video/mp4" />
                    </video>
                </div>
            </div>
        </>
    );

}

export default NecoleBringsYouSection;