import React from 'react';
import classNames from 'classnames';
import Image from '../../elements/Image';

const SmallSplit = ({
  title,
  text,
  imgsrc,
  alt,
  className,
  classNameImg,
  invert,
  ...props
}) => {

  const classes = classNames( 
      'small-split-container',
      'm-4',
      className
    );

  const imageSplit = <div className="p-4 small-split-image">
                      <Image
                          className={classNameImg}
                          src={imgsrc}
                          alt={alt}
                          width={74}
                          height={74} />
                      </div>;
  const textSplit = <div className="p-8 small-split-text">
                      <h6 className="text-color-primary">{title}<div className="fw-400">{text}</div></h6>
                      </div>;
  
  if(invert)
    return (
      <div
        {...props}
        className={classes}
        >
          {textSplit}       
          {imageSplit}
          </div>
        );
  else
    return (
      <div
        {...props}
        className={classes}
        >
          {imageSplit}
          {textSplit}       
          </div>
        );
}

export default SmallSplit;