import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

class PrivacyLoad extends React.Component
{
    componentDidMount() {
        window.scrollTo(0, 0)
    }

    render(){
        return <>
            <h2 className="text-color-primary">Privacy Policy</h2>

       Leaderonomics Digital Sdn Bhd (1270556-K) respects the privacy of individuals with regard to personal data and is committed to protecting the privacy of our users, and strives to provide a safe, secure user experience. This privacy policy is formulated in accordance with the Personal Data Protection Act 2010 (“Act”), which describes how your information is collected and used and your choices with respect to your Personal Data. The following discloses our information gathering and dissemination practices for this website, digital.leaderonomics.com.

            <div>
                <h4>Information Collected</h4>
                <ol>
                    <li>Our web servers do not automatically recognise specific information about individual users on our site. In addition, our servers do not automatically record information regarding a user’s e- mail address unless the user supplies it.</li>
                    <li>We collect information about users during the registration process for certain parts of our site; through their participation in certain activities, including contests, forums, and polls; and through the use of cookies. When you request pages from our server, it automatically collects some information about your preferences, including your IP address. We use this to help diagnose problems with our server, and to administer our site.</li>
                    <li>The user-supplied information collected through the registration process, surveys, contest entry forms, polls or other similar vehicles is used to improve the content of our site, or used for our marketing purposes. It is not shared with other organisations for commercial purposes unless specifically stated.</li>
                </ol>
            </div>

            <div>
                <h4>Purpose of Personal Data Collected</h4>
                <ol>
                    <li>
                        Personal data that you provide to us voluntarily on our website and its other related channels will be processed for the following purposes (“Purposes”):
                   <ul>
                            <li>to complete transactions with you, manage and verify your membership where applicable</li>
                            <li>to understand your needs and preferences</li>
                            <li>to develop, enhance, market and provide content to meet your needs</li>
                            <li>to enable you to participate in promotions and contests</li>
                            <li>to improve our services</li>
                            <li>to respond to requests or complaints.</li>
                        </ul>
                    </li>
                    <li>
                        Although the precise details of the personal data collected will vary according to the specific purpose (such as contests, polls, etc) whether via online or otherwise, we may typically collect the following personal data from or in relation to you:
                   <ul>
                            <li>Name (First Name and Last Name)</li>
                            <li>Address</li>
                            <li>Phone number(s)</li>
                            <li>Date of birth</li>
                            <li>Email address</li>
                            <li>Gender</li>
                            <li>Learning interests</li>
                            <li>Development interests</li>
                        </ul>
                    </li>
                </ol>
            </div>

            <div>
                <h4>Use and Disclosure</h4>
                <ol>
                    <li>We are responsible for the personal data under our control, including personal data disclosed by us to a Vendor (often referred to as the data processor). “Vendor” in this Privacy Policy means in relation to personal data any person or entity (other than an employee of Leaderonomics Digital Sdn Bhd) who processes the personal data on behalf of us.</li>
                    <li>We take every measure to provide a comparable level of protection for Personal Data should the information be processed by a Vendor.</li>
                    <li>We use and disclose aggregated non-personally identifying information collected through this Website as part of our organisation’s process of constantly improving this Website and/or business.</li>
                </ol>
            </div>

            <div>
                <h4>Storage and Retention of Personal Data</h4>
                <ol>
                    <li>Your personal data shall be stored in servers operated by us or our service providers.</li>
                    <li>Any personal data supplied by you will be retained by our organisation as long as necessary for the fulfilment of the purposes stated in ‘Purpose of Personal Data Collected’ above or is required to satisfy legal regulatory, accounting requirements or to protect our interests.</li>
                </ol>
            </div>

            <div>
                <h4>Confidentiality</h4>
                <ol>
                    <li>Personal data held by us will be kept confidential in accordance with this Privacy Policy pursuant to any applicable law that may from time to time be in force.</li>
                    <li>Any questions, comments, suggestions or information other than Personal Data sent or posted to this Website, or any part of this Website by visitors will be deemed voluntarily provided to us on a non-confidential and non-proprietary basis.</li>
                </ol>
            </div>

            <div>
                <h4>Links</h4>
                <ol>
                    <li>This site contains links to other sites. We are not responsible for the privacy practices or the content of such sites. We have also established relationships with partners but such relationships are generally technical in nature, or content collaborations.</li>
                </ol>
            </div>

       We reserve the right to amend this Privacy Policy from time to time without prior notice. We advise that you check this Privacy Policy on a regular basis.
        </>;
    }
}

const Privacy = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    'container',
    'invert-color',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
        <div className={innerClasses}>
        <PrivacyLoad />
        </div>
    </section>
  );
}

Privacy.propTypes = propTypes;
Privacy.defaultProps = defaultProps;

export default Privacy;