import React from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Image from '../elements/Image';
import TextLoop from "react-text-loop";

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const Hero = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  imageFill,
  ...props
}) => {
  
  const outerClasses = classNames(
    'hero section center-content top-banner',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'hero-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
        
      <div className="container">
        <div className={innerClasses}>
          <div className="hero-content center-content mt-32">
            <div className="hero-align m-32 center-content-mobile">
              <h1 className="mt-32 mb-16 center-content-mobile ta-l reveal-from-bottom" data-reveal-delay="200">
                <span className="fw-400">Meet </span> necole.
                </h1>
              <h2 className="h5-mobile m-0 mb-32 ta-l center-content-mobile reveal-from-bottom fw-400" data-reveal-delay="400">
                Accelerate your                 
                <br />
                <TextLoop interval={1800} mask={true}>
                  <span><em>career growth</em></span>
                  <span><em>future skills</em></span>
                  <span><em>professional development</em></span>
                  <span><em>future potential</em></span>
                </TextLoop>
                </h2>              
            </div>

            <div className="hero-image reveal-from-right mt-32">        
              <Image
                src={require('./../../assets/images/NECOLE_Mobile App.gif')}
                alt="NECOLE_Mobile App"
                width={400}                
              />
              {/* <video autoPlay loop muted playsInline  width={400}>
                <source src={require('./../../assets/images/gif-banner.mp4')} type="video/mp4" />
              </video> */}
            </div>
            
          </div>
        </div>
      </div>
    </section>
  );
}

Hero.propTypes = propTypes;
Hero.defaultProps = defaultProps;

export default Hero;