import React from 'react';
import classNames from 'classnames';
import Image from '../../elements/Image';

const ClientTestimonial = ({
  name,
  text,
  position,
  imgsrc,
  alt,
  className,
  classNameImg,
  ...props
}) => {

    const classes = classNames( 
        // 'client-testimonial',
        'm-4',        
        className
      );

  return (
    <div
      {...props}
      className={classes}         
      >
        <div className="pl-32 pr-32 center-content client-testimonial-container container">
          <h5 className="fw-400 client-testimonial-text ml-32 mr-32 invert-color">{text}</h5>
          <div className="p-4">
            <Image
                className={classNameImg}
                src={imgsrc}
                alt={alt}
                width={100}
                height={100} />
            </div>
            <h5 className="invert-color text-xs mb-4">{name}</h5>            
            <h5 className="invert-color text-xs fw-400 m-0">{position}</h5>            
          </div>
        
      </div>
  );
}

export default ClientTestimonial;