import React, {Component} from 'react';
// import sections
import Hero from '../components/sections/Hero';
import VideoSection from '../components/sections/VideoSection';
import Leader from '../components/sections/Leader';
import Time from '../components/sections/Time';
import FutureReady from '../components/sections/FutureReady';
import NecoleBringsYou from '../components/sections/NecoleBringsYou';
import FutureInvestment from '../components/sections/FutureInvestment';
import Pricing from '../components/sections/Pricing';
import Subscribe from '../components/sections/Subscribe';
import Testimonial from '../components/sections/Testimonial';
import Traits from '../components/sections/Traits';
import FAQ from '../components/sections/FAQ';

class Home extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectedCurrency:"USD",
      conversionRate: 0.24,
      priceMYR: 300
    }
  }

  setSelectedCurrencyOnParent = (_selectedCurrency, _priceMYR, _conversionRate) => {    
    this.setState({
      selectedCurrency: _selectedCurrency,
      priceMYR: _priceMYR,
      conversionRate: _conversionRate
    });
  }

  render() {
    return (
      <>
        <Hero className="illustration-section-01" />
        <VideoSection />
        <Leader />
        <Time />
        <NecoleBringsYou />
        <FutureReady />
        <FutureInvestment />
        <Pricing setSelectedCurrencyFromParent={this.setSelectedCurrencyOnParent.bind(this)} />
        <Subscribe selectedCurrency={this.state.selectedCurrency} conversionRate={this.state.conversionRate} priceMYR={this.state.priceMYR} />
        <Testimonial />
        <Traits imageFill={true} />
        <FAQ />
      </>
    );
  }
}

export default Home;