import React  from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const Leader = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    'section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );
  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <div className="container">
            <h2 className="mt-0 mb-16 text-color-primary fw-400 ta-c">Experience a holistic career-<br />accelerating journey!</h2>
            <p className="m-0 center-content">
            <video autoPlay loop muted playsInline width={576}> 
                <source src={require('./../../assets/images/NECOLE_Leadership52.mp4')} type="video/mp4" />
              </video>  
            </p>
          </div>
        </div>
        </div>
    </section>
  );
}

Leader.propTypes = propTypes;
Leader.defaultProps = defaultProps;

export default Leader;