import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
const axios = require('axios');

const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const basePrice = 300;
const baseCurrency = 'MYR';
const beforeDiscount = 1.33;


const defaultPrice = '';
const defaultSelection = 'USD';
const roundToDecimal = (num1, decimalPlaces) => ((Math.round(num1 * 100) / 100).toFixed(decimalPlaces))

class CurrencySelect extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ratesArray: [],
      priceMYR: basePrice,
      selection: defaultSelection,
      price: defaultPrice,
      prevPrice: roundToDecimal(defaultPrice * beforeDiscount, 2),
      pricePerDay: roundToDecimal(defaultPrice / 365, 2),
      json: { "rates": { "CAD": 0.3148361534, "HKD": 1.8561263064, "ISK": 32.9512604363, "PHP": 11.6131966766, "DKK": 1.5047405341, "HUF": 70.9220288273, "CZK": 5.2744253745, "GBP": 0.182293246, "RON": 0.9784300645, "SEK": 2.0961449047, "IDR": 3513.4817150828, "INR": 17.7435462025, "BRL": 1.3330301009, "RUB": 17.7891321487, "HRK": 1.5228334041, "JPY": 25.3219317929, "THB": 7.5223886631, "CHF": 0.2175389654, "EUR": 0.202154972, "MYR": 1.0, "BGN": 0.3953746942, "TRY": 1.7650959225, "CNY": 1.6555885742, "NOK": 2.1466432167, "NZD": 0.3650716639, "ZAR": 4.0554106778, "USD": 0.2394929953, "MXN": 5.2420199325, "SGD": 0.3275517011, "AUD": 0.3326257909, "ILS": 0.8145430287, "KRW": 284.2945802252, "PLN": 0.8891988598 }, "base": "MYR", "date": "2020-08-24" }
    };
    this.handleChange = this.handleChange.bind(this);
    this.setSelectedCurrency = this.setSelectedCurrency.bind(this);
  }

  getPrice(price, currency, isRound) {
    var _price = price * this.state.json.rates[currency];
    return isRound ? roundToDecimal(_price, 2) : _price;
  }

  getPrevPrice(price, currency, isRound) {
    var _price = price * beforeDiscount * this.state.json.rates[currency];
    return isRound ? roundToDecimal(_price, 2) : _price;
  }

  getPricePerDay(price, isRound) {
    var _price = price / 365;
    return isRound ? roundToDecimal(_price, 2) : _price;
  }

  setSelectedCurrency(e) {
    e.preventDefault();
    this.props.setSelectedCurrencyFromParent(e.target.value, this.state.priceMYR, this.state.json.rates[e.target.value]);
  }

  updateState(self, priceMYR, selection)
  {
    var price = self.getPrice(priceMYR, selection, true);
    var pricePerDay = self.getPricePerDay(price, true);
    var prevPrice = self.getPrevPrice(priceMYR, selection, true);
    self.setState({
      selection: selection,
      price: price,
      prevPrice: prevPrice,
      pricePerDay: pricePerDay
    });
  }

  componentDidMount() {

    // Get updated currency exchange here
    var urlCurrency = 'https://api.exchangeratesapi.io/latest?base=' + baseCurrency;
    var urlPrice = 'https://api.digital.leaderonomics.com/api/course?id=13&size=1';
    var self = this;

    // Get conversion rate
    axios.get(urlCurrency)
      // store conversion rate api response
      .then(function (response) {
        self.setState({ json: response.data });
      })
      // catch conversion rate errors
      .catch(function (error) {
        console.error(error);
      })
      // Get pricing from server
      .then(axios.get(urlPrice)
        // store pricing 
        .then(function (response) {
          self.updateState(self, response.data.data[0].myr_price, self.state.selection);
          self.props.setSelectedCurrencyFromParent(self.state.selection, self.state.priceMYR, self.state.json.rates[self.state.selection]);
        })
        // catch pricing errors
        .catch(function (error) {
          console.error(error);
        })
      )
      // Sort and store conversion rates
      .then(function () {
        var arr = [];
        var ratesJson = self.state.json.rates;

        Object.keys(ratesJson).forEach(function (key) {
          arr.push({ key: key, value: key, rate: ratesJson[key] });
        })

        arr.sort((a, b) => {
          if (a.value > b.value)
            return 1;
          else
            return -1;
        });

        self.setState({ ratesArray: arr });
        self.props.setSelectedCurrencyFromParent(self.state.selection, self.state.priceMYR, self.state.json.rates[self.state.selection]);
      });
  }

  handleChange(event) {
    this.updateState(this, this.state.priceMYR, event.target.value);
    this.setSelectedCurrency.bind(this);
    this.setSelectedCurrency(event);
  }

  render() {
    return (
      <>
        <del>${this.state.prevPrice}</del>
        <h2 className="invert-color text-color-low m-0">${this.state.price}/year</h2>
        <div>Just &lt;${this.state.pricePerDay} a day!</div>
        <div className="container-xs p-24 width-100">
          <select className="form-select center-content" id="color" onChange={this.handleChange} value={this.state.selection}>
            {this.state.ratesArray.map(element =>
              <option key={element.key}>{element.value}</option>
            )}
          </select>
        </div>
      </>
    );
  }
}

class Pricing extends Component {

  outerClasses = classNames(
    'section',
    this.props.topOuterDivider && 'has-top-divider',
    this.props.bottomOuterDivider && 'has-bottom-divider',
    this.props.hasBgColor && 'has-bg-color',
    this.props.invertColor && 'invert-color',
    this.props.className
  );
  innerClasses = classNames(
    'section-inner',
    this.props.topDivider && 'has-top-divider',
    this.props.bottomDivider && 'has-bottom-divider'
  );

  render() {
    return (
      <section
        // {...props}
        className={this.outerClasses}        
      >
        <div className="container center-content">
          <div className={this.innerClasses}>
            <CurrencySelect setSelectedCurrencyFromParent={this.props.setSelectedCurrencyFromParent} />
            <div className="text-xxs">*The price shown is an estimation based on current exchange rates</div>
            <div className="spacer-24"></div>
            <div className="container-xs center-content text-xs ta-l invert-color text-color-low small-split-container">
              <div className="small-split-alt-left pr-8">✓</div>
              <div className="small-split-alt-right ta-l"><b>BONUS</b> access to live webinars every month, a value of USD100. Plus we are giving you access to all of our past recorded webinars from industry experts at no charge.</div>
            </div>
          </div>
        </div>

      </section>
    );
  }
}

Pricing.propTypes = propTypes;
Pricing.defaultProps = defaultProps;

export default Pricing;