class SubscribeButton extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      isSubmitting: false,
      price: roundToDecimal(this.props.priceMYR * this.props.conversionRate),
      priceMYR: this.props.priceMYR,
      selectedCurrency: this.props.selectedCurrency,
      firstName:"",
      firstNameHint: "",
      firstNameStatus: "",
      lastName: "",
      lastNameHint: "",
      lastNameStatus: "",
      email: "",
      emailHint: "",
      emailStatus: "",
      mobile: "",
      mobileHint: "",
      mobileStatus: "",
      promoCode: "",
      promoCodeHint: "",
      promoCodeStatus: "",
      interestsHint: "",
      interestsStatus: "",
      interests: [],
      version: "",
      CID: "",
      v_currency: "",
      v_amount: "",
      v_cartid: "",
      v_productdesc: "",
      callbackurl: "",
      returnurl: "",
      signature: "",
      phoneCode: "Malaysia (+60)"
    };
    this.onChangePromoCode = this.onChangePromoCode.bind(this);
    this.applyPromo = this.applyPromo.bind(this);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangeMobile = this.onChangeMobile.bind(this);
    this.onChangePhoneCode = this.onChangePhoneCode.bind(this);
    this.onChangeInterest = this.onChangeInterest.bind(this);
    this.onCheckOut = this.onCheckOut.bind(this);
  }

  // Promo code
  onChangePromoCode(e) {
    this.setState({ promoCode: e.target.value });
  }

  applyPromo(e) {
    e.preventDefault();
    if (this.state.promoCode.length === 0) {
      this.setState({
        promoCodeHint: "Please enter a promo code",
        promoCodeStatus: "error",
        price: roundToDecimal(defaultPrice, 2),
        selectedCurrency: defaultCurrency
      });
      return;
    }

    this.setState({ promoCodeHint: "", promoCodeStatus: "" }); // reset hint
    var url = 'https://api.digital.leaderonomics.com/api/promo-code/apply?course_id=13&promo_code=' + this.state.promoCode;
    var self = this;

    axios.get(url)
      .then(function (response) {
        if (response.data.data.promo_discount > 0) {
          self.setState({
            priceMYR: roundToDecimal(response.data.data.myr_price, 2),
            price: roundToDecimal(response.data.data.myr_price * self.props.conversionRate, 2)
          });

        }
        else {
          self.setState({ promoCodeHint: "Invalid code", promoCodeStatus: "error" });
        }
      })
      .catch(function (error) {
        self.setState({ promoCodeHint: "Error checking code", promoCodeStatus: "error" });
        console.error(error);
      });
  }

  // Verification helper
  isFieldNotEmpty(value, hintName, statusName)
  {
    var isOK = value.length > 0;

    if(isOK)
      this.setState({
        [hintName]: "",
        [statusName]: ""
      });
    else
      this.setState({
        [hintName]: requiredText,
        [statusName]: "error"
      });
      
    return isOK;
  }

  // First name
  onChangeFirstName(e) {
    var value = e.target.value;
    this.setState({ firstName: value });
    this.isFieldNotEmpty(value, 'firstNameHint', 'firstNameStatus');
  }

  // Last name
  onChangeLastName(e) {
    var value = e.target.value;
    this.setState({ lastName: value });
    this.isFieldNotEmpty(value, 'lastNameHint', 'lastNameStatus');
  }

  // Email
  isValidEmail(value, hintName, statusName){
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    var isOK = re.test(String(value).toLowerCase());

    if(isOK)
      this.setState({
        [hintName]: "",
        [statusName]: ""
      });
    else
      this.setState({
        [hintName]: "Please enter a valid email address",
        [statusName]: "error"
      });
      
    return isOK;
  }

  onChangeEmail(e) {
    var value = e.target.value;
    this.setState({ email: value });
    this.isFieldNotEmpty(value, 'emailHint', 'emailStatus') && 
    this.isValidEmail(value, 'emailHint', 'emailStatus');
  }

  // Mobile Country Code  
  onChangeMobileCountryCode(e) {    
    var value = e.target.value;
    this.setState({ mobile: value });    
    // this.isFieldNotEmpty(value, 'mobileHint', 'mobileStatus') &&    
    this.isValidMobile(value, 'mobileHint', 'mobileStatus');
  }
  // Mobile
  isValidMobile(value, hintName, statusName){
    const re = /^(\s*|\d+)$/;
    // const re = /^[0-9\b]+$/;
    var isOK = re.test(String(value));
    
    if(isOK)
      this.setState({
        [hintName]: "",
        [statusName]: ""
      });
    else
      this.setState({
        [hintName]: "Please enter a valid phone number",
        [statusName]: "error"
      });
      
    return isOK;
  }

  onChangeMobile(e) {    
    var value = e.target.value;
    this.setState({ mobile: value });    
    // this.isFieldNotEmpty(value, 'mobileHint', 'mobileStatus') && 
    this.isValidMobile(value, 'mobileHint', 'mobileStatus');
  }
  
  onChangePhoneCode(e) {    
    this.setState({ phoneCode: e.target.value });
  }

  renderPhoneCodes(codes) {
    return codes.map((code, i) => {
      return (
        <>
          <option value={code.name + ' (' + code.dial_code + ')'}>
              {code.name + ' (' + code.dial_code + ')'}
            </option>
        </>);
    });
  }

  // Interest
  isValidInterests(){        
    if(this.state.interests.length > 3)
    {
      this.setState({
        interestsStatus: "error",
        interestsHint: "You have to choose at most 3 interests"
      });      
      return false; 
    }
    else if(this.state.interests.length === 0){
      this.setState({
        interestsStatus: "error",
        interestsHint: requiredText
      });     
      return false; 
    }
    else
    {      
      this.setState({
        interestsStatus: "",
        interestsHint: ""
      });
    }
    return true;
  }

  onChangeInterest(e){
    const checked = e.target.checked;
    const name = e.target.name;
    var updatedInterests = this.state.interests;
    const index = updatedInterests.indexOf(name);

    // checkbox checked
    if(checked)
    {
      // add to selection
      if (index < 0) {
        updatedInterests.push(name);
      }
    }
    // checkbox unchecked
    else{
      if (index > -1) {
        updatedInterests.splice(index, 1);
      }
    }

    this.setState({ interests: updatedInterests });
    this.isValidInterests();
  }

  renderInterests(interests) {
    return interests.map((interest, i) => {
      return (
        <>
          <Checkbox 
            className="ml-12 checkbox-flex" 
            name={interest}            
            onChange={this.onChangeInterest.bind(this)}                  
            >
              {interest}
            </Checkbox><br />
        </>);
    });
  }
  
  // Modal 
  onCheckOut(e){
    e.preventDefault();
    this.setState({ isSubmitting: true });

    var isOK =  this.isFieldNotEmpty(this.state.firstName, 'firstNameHint', 'firstNameStatus');
        isOK =  this.isFieldNotEmpty(this.state.lastName, 'lastNameHint', 'lastNameStatus') && isOK;
        isOK =  this.isFieldNotEmpty(this.state.email, 'emailHint', 'emailStatus') && isOK;
        isOK =  this.isValidEmail(this.state.email, 'emailHint', 'emailStatus') && isOK;                
        isOK =  this.isValidMobile(this.state.mobile, 'mobileHint', 'mobileStatus') && isOK;
        isOK =  this.isValidInterests() && isOK;
    
    if(!isOK)  
    {
      this.setState({ isSubmitting: false });      
      return;
    }             
      
    // Prevent the button from being pressed again while we're sending the POST message
    var guestPurchaseURL = 'https://api.digital.leaderonomics.com/api/guest-purchase';    
    var self = this;
    var str = this.state.phoneCode;
    var phoneCodeSubstring = str.substring(str.lastIndexOf("+") + 1, str.lastIndexOf(")"));
    var mobilenumber = phoneCodeSubstring + this.state.mobile;
        
    axios.post( guestPurchaseURL,{
                // data
                course_id: 13,
                first_name: this.state.firstName,
                last_name: this.state.lastName,
                mobile_number: mobilenumber,
                email: this.state.email,
                interests: this.state.interests.join(','),
                promo_code: this.state.promoCode
              })
    .then(function (response) {
      var data = response.data.data;
      
      self.setState({
        version: data.version,
        CID: data.CID,
        v_currency: data.v_currency,
        v_amount: data.v_amount,
        v_cartid: data.v_cartid,
        v_productdesc: data.v_productdesc,
        callbackurl: data.callbackurl,
        returnurl: data.returnurl,
        signature: data.signature,
        isSubmitting : false 
      },()=>{
        document.getElementById("subscribeForm").submit()
      });
    })
    .catch(function (error) {
      console.error(error);
    });
  }

  closeModal = (e) => {
    e.preventDefault();
    this.setState({ showModal: false });
  }

  showModal = (e) => {
    e.preventDefault();
    this.setState({
      showModal: true,
      price: roundToDecimal(this.props.priceMYR * this.props.conversionRate, 2),
      selectedCurrency: this.props.selectedCurrency,
      promoCode: "",
      promoCodeHint: "",
      promoCodeStatus: ""
    });
  }

  render() {
    return <>
      <Button wideMobile 
              color="primary tt-u button-vertical" 
              className="subscribe-button"                             
              onClick={this.showModal}>
        Subscribe Now
        </Button>
      <Modal
        show={this.state.showModal}
        handleClose={this.closeModal}
        className="invert-color"

      >
        <form 
          className="ta-l invert-color" 
          id="subscribeForm" 
          action="https://api.gkash.my/api/paymentform.aspx" 
          method="post">
          <fieldset>
            <div className="container border-primary">
              <h4>BILLING INFORMATION</h4>
              <div className="mb-16">
                <Input
                  type="text"
                  name="v_firstname"
                  placeholder="First name*"
                  label="First name"
                  value={this.state.firstName}
                  hint={this.state.firstNameHint}
                  status={this.state.firstNameStatus}
                  onChange={this.onChangeFirstName}
                />
              </div>
              <div className="mb-16">
                <Input
                  type="text"
                  name="v_lastname"
                  placeholder="Last name*"
                  label="Last name"
                  value={this.state.lastName}
                  hint={this.state.lastNameHint}
                  status={this.state.lastNameStatus}
                  onChange={this.onChangeLastName}
                />
              </div>
              <div className="mb-16">
                <Input
                  type="email"
                  name="v_billemail"
                  placeholder="Email*"
                  label="Email"
                  value={this.state.email}
                  hint={this.state.emailHint}
                  status={this.state.emailStatus}
                  onChange={this.onChangeEmail}
                />
              </div>

              <div className="mb-16">
                <label for="v_billphone" className="form-label">Mobile Number</label>
                
                <div className="mobile-phone-container">
                <select class="form-select phone-code" id="phone-code" value={this.state.phoneCode} onChange={this.onChangePhoneCode}>
                  {this.renderPhoneCodes(phonecodes)}
                </select>
                <div className="spacer-12-mobile" />

                <Input
                  id="v_billphone"
                  type="number"
                  name="v_billphone"
                  placeholder="Mobile number"   
                  wrapperClassName="phone-number"               
                  value={this.state.mobile}
                  hint={this.state.mobileHint}
                  status={this.state.mobileStatus}
                  onChange={this.onChangeMobile}
                />
                </div>
              </div>

              <div className="mb-16 form-label">
                Top 3 learning interests
                <FormHint status={this.state.interestsStatus}>{this.state.interestsHint}</FormHint>
              </div>

              <div className="m-12 checkbox-flex-parent">
                {this.renderInterests(allInterests)}
              </div>

            </div>
            <div className="spacer-12" />
            <div className="container border-primary">
              <h4>ORDER SUMMARY</h4>
              NECOLE - LEADERSHIP DEVELOPMENT PLATFORM <br />
              <div className="spacer-12"><b>
                <div className="small-split-half">Item Price ({this.props.selectedCurrency}) </div>
                <div className="small-split-half">{this.state.price} </div>
              </b></div>
              <div className="text-xxs mb-16">*The price shown is an estimation based on current exchange rates</div>

              <div>
                <div className="small-split-half">
                  <Input
                    type="text"                    
                    placeholder="Promo Code"
                    value={this.state.promoCode}
                    onChange={this.onChangePromoCode}
                    status={this.state.promoCodeStatus}
                    hint={this.state.promoCodeHint}
                  />
                </div>
                <div className="apply-button small-split-half" >
                  <Button color="primary" onClick={this.applyPromo}>APPLY</Button>
                </div>
              </div>

              <div className="spacer-12 spacer-4-mobile mt-16 h4 h5-mobile">
                <div className="small-split-half">TOTAL ({this.props.selectedCurrency}) </div>
                <div className="small-split-half">{this.state.price}</div>
              </div>

              <div className="spacer-12 spacer-4-mobile mb-0 h4 h5-mobile">
                <div className="small-split-half">TOTAL (MYR) </div>
                <div className="small-split-half">{roundToDecimal(this.state.priceMYR, 2)}</div>
              </div>
              <div className="text-xxs mb-24 ">* Transaction currency will be in MYR equivalent </div>

            </div>

            <input type="hidden" name="version" value={this.state.version} />
            <input type="hidden" name="CID" value={this.state.CID} />
            <input type="hidden" name="v_currency" value={this.state.v_currency} />
            <input type="hidden" name="v_amount" value={this.state.v_amount} />
            <input type="hidden" name="v_cartid" value={this.state.v_cartid} />
            <input type="hidden" name="v_productdesc" value={this.state.v_productdesc} />
            <input type="hidden" name="callbackurl" value={this.state.callbackurl} />
            <input type="hidden" name="returnurl" value={this.state.returnurl} />
            <input type="hidden" name="signature" value={this.state.signature} />

            <div className="mt-24">
              <div className="button-group">
                <Button color="primary" loading={this.state.isSubmitting} onClick={this.onCheckOut}>CHECK OUT</Button>
                <Button color="dark" onClick={this.closeModal}>Cancel</Button>
              </div>
            </div>
          </fieldset>
        </form>
      </Modal>
    </>;
  };
}


const Subscribe = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  selectedCurrency,
  conversionRate,
  priceMYR,
  ...props
}) => {

  const outerClasses = classNames(
    // 'testimonial section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider',
    className
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="center-content p-16 subscribe-button-bg" id="subscribe">
        <SubscribeButton selectedCurrency={selectedCurrency} conversionRate={conversionRate} priceMYR={priceMYR} />
      </div>
    </section>
  );
}

Subscribe.propTypes = propTypes;
Subscribe.defaultProps = defaultProps;

export default Subscribe;