// You can live edit this code below the import statements
import React from 'react';
import Fade from 'react-reveal/Fade';

const fadeWrapper = (i, array, show)=>{
    return <> 
        <Fade bottom opposite when={show}>
            {array[i]}
        </Fade>
    </>;
};

class VerticalTextFade extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            textArray: this.props.textArray,
            i: 0,
            interval: this.props.interval / 2 ,
            fadeHTML: fadeWrapper(0, this.props.textArray, true),
            show: true,
            pause: true
        };
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.scroll(),
            this.state.interval
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    scroll() {

        var iNew = this.state.i;
        var show = this.state.show;
        var pause = this.state.pause;

        
        // If not shown, show
        if(show && pause)
        {   
            pause = false;
            show = true;
        }
        else if(show && !pause)           
        {
            pause = true;
            show = false;
        }
        else {         
            show = true;
            pause = true;
            if (iNew >= this.state.textArray.length - 1)
                iNew = 0;
            else
                iNew++;
        }

        this.setState((state) => ({
            i: iNew,
            fadeHTML: fadeWrapper(iNew, state.textArray, show),
            show: show, 
            pause: pause
        }));
    }

    render() {
        return (
            <div className={this.props.className}>
                {this.state.fadeHTML}
            </div>
        );
    }
}

export default VerticalTextFade;
