import React from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import ClientTestimonial from '../sections/partials/ClientTestimonial';
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';

const propTypes = {
  ...SectionTilesProps.types
}

const defaultProps = {
  ...SectionTilesProps.defaults
}

const Testimonial = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {

  const outerClasses = classNames(
    // 'testimonial section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider',
    className
  );

  const slides = [
          { description:  <ClientTestimonial 
                            name="Muhammad Nizam Ahmad" 
                            position="Head of Client Engagement"
                            text={<>Whenever I need quick learning with light-weighted content on-the-go, I would use <span className="text-color-primary">necole</span>. It offers the latest trends to my learning interests and skills crucial for my career development. While FB and Instagram are my leisure fillers, <span className="text-color-primary">necole</span> is my development and future achievements platform.</>}
                            imgsrc={require('./../../assets/images/nizam-cropped.png')} 
                            />},
          { description:  <ClientTestimonial 
                            name="Shasidharan Kesavan" 
                            position="Multimedia Producer"
                            text={<>I never thought learning could be so fun.
                            When I first started using <span className="text-color-primary">necole</span>, I was amazed at how much content I wasn't aware of and therefore, missing out on.
                            The AI has changed the way I approach learning to the point where I actually look forward to it now.</>}
                            imgsrc={require('./../../assets/images/shasi-cropped.png')} 
                            />},
          { description:  <ClientTestimonial 
                            name="Parthiban Vijayaraghavan" 
                            position="CEO"
                            text={<>My acumen on subjects which I desired to learn and be good at, has increased significantly in the last six months and it has shown in my performance as well. Instead of browsing Google, YouTube and other search engines, which throws all kinds of contents makes it harder to choose and sometimes waste time in wrong contents, I rely on <span className="text-color-primary">necole</span> to filter out non-relevant and non-contextual contents and curates high-quality contents from diverse sources.</>}
                            imgsrc={require('./../../assets/images/parthi-cropped.png')} 
                            />},
          { description:  <ClientTestimonial 
                            name="Yap Jo Yee" 
                            position="Leadership & Management, Technical Development & Sales"
                            text={<>Every single time I hit a mental block, for example, when I am trying to approach a business prospect, or even cracking my head on how to thank a customer, I hit up <span className="text-color-primary">necole</span> and ask her what she thinks. And she just helps me generate ideas with her smart cards and pathways. I then write down a comment, so that I remember what I have learnt, and I can then use it again for future endeavors. It really is helping me accelerate my learning curve, and its influencing my other team mates to do so too, when they see me having a good experience.</>}
                            imgsrc={require('./../../assets/images/joyee-cropped.png')} 
                            />}
        ];
  
  return (
    <section
      {...props}
      className={outerClasses}
    >                
      <div className="client-testimonial">        
          <Slider autoplay={2000} touchDisabled>
            {slides.map((slide, index) => 
              <div key={index}>                
                <div>{slide.description}</div>
                </div> 
              )}
            </Slider>

      </div>        
    </section>
  );
}

Testimonial.propTypes = propTypes;
Testimonial.defaultProps = defaultProps;

export default Testimonial;