import React from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import Image from '../../elements/Image';

const Logo = ({
  className,
  isAlternateLogo,
  ...props
}) => {

  const classes = classNames(
    'brand',
    className
  );

  return (
    <div
      {...props}
      className={classes}
    >
      <h1 className="m-0">
        <Link to={`${process.env.PUBLIC_URL}/`}>
          <Image
            src={ isAlternateLogo ? 
                  require('./../../../assets/images/NECOLE.png') : 
                  require('./../../../assets/images/NECOLE_White.png') }
            alt="Open"
            width={128}
            height={128} />
        </Link>
      </h1>
    </div>
  );
}

export default Logo;