import React, { useRef, useEffect } from 'react';
import { useLocation, Switch } from 'react-router-dom';
import AppRoute from './utils/AppRoute';
import ScrollReveal from './utils/ScrollReveal';
import ReactGA from 'react-ga';

// Layouts
import LayoutDefault from './layouts/LayoutDefault';
import LayoutNoNav from './layouts/LayoutNoNav';

// Views 
import Home from './views/Home';
import Terms from './views/Terms';
import Contact from './views/Contact';
import Privacy from './views/Privacy';

// Initialize Google Analytics
ReactGA.initialize(process.env.REACT_APP_GA_CODE);

const trackPage = page => {
  ReactGA.set({ page });
  ReactGA.pageview(page);
};

const App = () => {

  const childRef = useRef();
  let location = useLocation();

  useEffect(() => {
    const page = location.pathname;
    document.body.classList.add('is-loaded')
    childRef.current.init();
    trackPage(page);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location]);
  
  return (
    <ScrollReveal
      ref={childRef}
      children={() => (
        <Switch>
          <AppRoute exact path={`${process.env.PUBLIC_URL}/`} component={Home} layout={LayoutDefault} />
          <AppRoute exact path={`${process.env.PUBLIC_URL}/terms`} component={Terms} layout={LayoutNoNav} />
          <AppRoute exact path={`${process.env.PUBLIC_URL}/contact`} component={Contact} layout={LayoutNoNav} />
          <AppRoute exact path={`${process.env.PUBLIC_URL}/privacy`} component={Privacy} layout={LayoutNoNav} />
        </Switch>
      )} />
  );
}

export default App;