import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Logo from './partials/Logo';
import SmoothScroll from '../elements/SmoothScroll';
import Headroom from 'react-headroom';

const propTypes = {
  navPosition: PropTypes.string,
  hideNav: PropTypes.bool,
  hideSignin: PropTypes.bool,
  bottomOuterDivider: PropTypes.bool,
  bottomDivider: PropTypes.bool
}

const defaultProps = {
  navPosition: '',
  hideNav: false,
  hideSignin: false,
  bottomOuterDivider: false,
  bottomDivider: false
}

const headerHeight = 100;

class HeaderButton extends React.Component {
  state = {
    isTop: true,
    buttonClass: "header-nav-toggle mr-32"
  };

  getButtonClass(isTop) {
    let className = "header-nav-toggle mr-32";
    if (!isTop)
      className = "header-nav-toggle-hamburg " + className;

    return className;
  }

  componentDidMount() {
    document.addEventListener('scroll', () => {
      const isTop = window.scrollY < headerHeight;
      if (isTop !== this.state.isTop) {
        this.setState({
          isTop: (isTop),
          buttonClass: this.getButtonClass(isTop)
        });
      }
    });
  }

  render() {
    return (
      <button
        ref={this.props.hamburger}
        className={this.state.buttonClass}
        onClick={this.props.isActive ? this.props.closeMenu : this.props.openMenu}
      >
        <span className="screen-reader">Menu</span>
        <span className="hamburger">
          <span className="hamburger-inner"></span>
        </span>
      </button>
    );

  }
}

class LogoClass extends React.Component {
  state = {
    isAlternateLogo: false
  };

  render() {
    return (
      <Logo isAlternateLogo={this.state.isAlternateLogo} />
    );
  }
}

const Header = ({
  className,
  navPosition,
  hideNav,
  hideSignin,
  bottomOuterDivider,
  bottomDivider,
  ...props
}) => {

  const [isActive, setIsactive] = useState(false);

  const nav = useRef(null);
  const hamburger = useRef(null);

  useEffect(() => {
    isActive && openMenu();
    document.addEventListener('keydown', keyPress);
    document.addEventListener('click', clickOutside);
    return () => {
      document.removeEventListener('keydown', keyPress);
      document.addEventListener('click', clickOutside);
      closeMenu();
    };
  });

  const openMenu = () => {
    document.body.classList.add('off-nav-is-active');
    nav.current.style.maxHeight = nav.current.scrollHeight + 'px';
    setIsactive(true);
  }

  const closeMenu = () => {
    document.body.classList.remove('off-nav-is-active');
    nav.current && (nav.current.style.maxHeight = null);
    setIsactive(false);
  }

  const keyPress = (e) => {
    isActive && e.keyCode === 27 && closeMenu();
  }

  const clickOutside = (e) => {
    if (!nav.current) return
    if (!isActive || nav.current.contains(e.target) || e.target === hamburger.current) return;
    closeMenu();
  }

  const classes = classNames(
    'site-header',
    bottomOuterDivider && 'has-bottom-divider',
    className
  );

  return (
    <>
      <Headroom>
        <header
          {...props}
          className={classes}
        >
          <div>
            <div className={
              classNames(
                'site-header-inner',
                bottomDivider && 'has-bottom-divider'
              )}>
              <LogoClass />
              {!hideNav &&
                <>
                  <HeaderButton closeMenu={closeMenu} openMenu={openMenu} hamburger={hamburger} isActive={isActive} />
                  <nav
                    ref={nav}
                    className={
                      classNames(
                        'header-nav',
                        isActive && 'is-active'
                      )}>
                    <div className="header-nav-inner">
                      <ul className={
                        classNames(
                          'list-reset text-xs',
                          navPosition && `header-nav-${navPosition}`
                        )}>
                        <li>
                          <SmoothScroll to="benefits" onLinkClick={closeMenu}>Benefits</SmoothScroll>
                        </li>
                        <li>
                          <SmoothScroll to="pricing" onLinkClick={closeMenu}>Pricing</SmoothScroll>
                        </li>
                        <li>
                          <SmoothScroll to="faq" onLinkClick={closeMenu}>FAQ</SmoothScroll>
                        </li>
                        <li>
                          <a href={`${process.env.PUBLIC_URL}/contact`} target="_blank" rel="noopener noreferrer" onClick={closeMenu}>Contact Us</a>
                        </li>
                        <li>
                          <SmoothScroll to="subscribe" onLinkClick={closeMenu}>
                            <div className="button-dark button-sm subscribe-header">Subscribe</div>
                          </SmoothScroll>
                        </li>
                      </ul>
                    </div>
                  </nav>
                </>}
            </div>
          </div>
        </header>
      </Headroom>
    </>
  );
}

Header.propTypes = propTypes;
Header.defaultProps = defaultProps;

export default Header;